<template>
  <el-row  :gutter="20">
    <el-col :span="10">
ddd
    </el-col>
    <el-col :span="8">
      <ciycle :slide="ciycle" @on-change="tel"></ciycle>
    </el-col>
    <el-col :span="6">
      <div class="user_selection">
        <p> <el-checkbox v-model="checked"></el-checkbox></p>
        <p><strong></strong>
          <!--1-->
          <keep-alive>
          <radioSel :selections="radioList" @on-change="onParmsChange('buyType',$event)"></radioSel>
          </keep-alive>
        </p>
        <p><strong></strong>
          <!--2-->
          <multiSel  :selections="multiList" @on-change="onParmsChange('buyList',$event)"></multiSel>
        </p>
        <p class="count"><strong></strong><br/>
          <!--3-->
          <selection class="mySel" :selection="selName" @on-change="onParmsChange('buyName',$event)"></selection>
        </p>
        <p><strong></strong>
          <!--4-->
          <Number :num="number"  @on-change="onParmsChange('buyCount',$event)"></Number>
        </p>
        <p>
          <strong></strong><span>{{price}}</span>
        </p>
        <el-button class="mybtn" type="warning" @click="getPrice"><i></i></el-button>
        <el-button  type="danger" @click="isShowPopup"><i></i></el-button>
        <div class="tips">
          <h5><a href="javascript:"></a></h5>
          <h5><a href="javascript:"></a></h5>
        </div>
        <el-button class="mybtn" type="info"></el-button>
      </div>
      <div class="recharge">
        <el-button class="mybtn" type="info"  ></el-button>
      </div>
    </el-col>
    <Popup :isShow="isPopup" @on-close="noShowPopup">
      <table class="Product-table" width="300" border="1" cellspacing="1" cellpadding="1">
        <tbody>
        <tr class="title">
          <td></td>
          <td></td>
          <td></td>
          <td></td>
          <td></td>
        </tr>
        <tr>
          <td>01</td>
          <td>{{ buyType.name }}</td>
          <td>{{ buyCount }}</td>
          <td>{{buyName.name}}</td>
          <td>{{price}}</td>
        </tr>
        </tbody>
      </table>
      <banks :bankList="banksList" @on-change="toChooseBank"></banks>
      <p class="clearfix">
      <el-button type="success" @click="isShowSure"></el-button>
      <el-button type="info"></el-button>
      </p>
    </Popup>
    <showInfo :isaShow="isSure" @on-close="closeSure" @on-change="toDetail"></showInfo>
  </el-row>
</template>

<script type="text/ecmascript-6">
  import selection from '@/components/common/base/selection'
  import radioSel from '@/components/common/base/radioSel'
  import multiSel from '@/components/common/base/multiSel'
  import ciycle from '@/components/common/base/ciycle'
  import showInfo from '@/components/common/base/ShowInfo'
  import Number from '@/components/common/base/counterNumber'
  import Popup from '@/components/common/Popup'
  import banks from '@/components/common/BanksGroup'
  import axios from 'axios'
    export default{
      data(){
      return{
        price:0,
        buyType:{},
        buyList:[],
        buyName:{},
        buyCount:0,

        isPopup:false,
        isSure:false,


        number:0,
        ciycle:[3,3,3,3,3],
        checked:false,
        selName:[
          {name:"",type:"easy",value:"0"},
          {name:"",type:"base",value:"1"},
          {name:"",type:"difficult",value:"2"},
        ],
        radioList:[
          {name:"",type:"easy",value:"0"},
          {name:"",type:"base",value:"1"},
          {name:"",type:"middle",value:"2"},
          {name:"",type:"difficult",value:"3"},
        ],
        multiList:[
          {name:"",type:"easy",value:"50"},
          {name:"",type:"base",value:"100"},
          {name:"",type:"middle",value:"200"},
          {name:"",type:"difficult",value:"300"},
        ],
        banksList:[
          {name:"",id:'101',href:"javascript:",className:"zhongguo"},
          {name:"",id:'201',href:"javascript:",className:"gongshang"},
          {name:"",id:'301',href:"javascript:",className:"jianshe"},
          {name:"",id:'401',href:"javascript:",className:"nongye"},
          {name:"",id:'501',href:"javascript:",className:"guangfa"},
          {name:"",id:'601',href:"javascript:",className:"xingye"}
        ],
        orderId:"101"
      }
    },
  components:{
    selection,radioSel,multiSel,ciycle,Number,Popup,banks,showInfo
  },
  methods:{
    tel(data){
      let i=0;
      for(let o=0;o<data.length;o++){
        if(data[o]==4){
          i++;
        }
      }
      alert(i)
    },
    num(data){
      this.number=data;
     alert(this.number)
    },
    onParmsChange(attr,value){
      this[attr]=value;
      this.getPrice()
    },
    getPrice(){
      let buyArray= _.map(this.buyList,(idx)=>{
        return idx.value
      })
      //
      let parms={
        buyType:this.buyType.value,
        buyCount:this.buyCount,
        buyName:this.buyName.value,
        buyList: buyArray.join(","),
        orderId: this.orderId
        //
      }
      //
      axios.get("http://localhost:8080/static/abc.json",parms)
        .then((res)=>{
          this.price=res.data.price
          console.log(parms)
        }).catch((error)=>{
          console.log(error)
        })

    },
    isShowPopup(){
      this.isPopup=true
    },
    noShowPopup(){
      this.isPopup=false
    },
    isShowSure(){
      axios.get("http://localhost:8080/static/abc.json").then((res)=>{
        console.log(res.data.name)
      })
      this.isPopup=false;
      this.isSure=true;

    },
    noShowSure(){
      this.isSure=false
    },
    toChooseBank(data){
      this.orderId=data.id;
      console.log(this.orderId)
    },
    closeSure(){
      this.isSure=false;
    },
    toDetail(){
      this.$router.push({path:'/orderlist'})
    }

  },
  mounted:function(){
    this.buyType=this.radioList[0],
    this.buyCount=1,
    this.buyList=[this.multiList[0]],
    this.buyName=this.selName[0],
      this.getPrice()
  }
    }
</script>


<style >

.el-checkbox{
  margin-right: 5px;
}
.user_selection,.recharge{
  box-sizing: border-box;
  padding: 16px;
  border: 1px solid #dddddd;
}
  .user_selection p{
    margin-top: 16px;
    text-align: left;
  }
  .user_selection strong{
    font-weight: bold;
    color: #C21F39;
  }
  .mybtn{
    width: 208px;
    height: 36px;
    margin-bottom: 10px;
    margin-top: 76px;
  }
  .tips{
    margin: 0 auto;
    width: 208px;
    height: 78px;
    padding: 20px 0;
    border-top:1px solid slategray ;
    border-bottom:1px solid slategray ;
  }
  .tips h5{
    margin-top: 10px;
  }
  .recharge{
    margin-top: 10px;
  }
  .count{
    position: relative;
  }
  .mySel{
    position: absolute;
    left: 30%;
    top: 0;
  }
  .Product-table{
    margin: 40px auto;
    border: 1px solid #e0e0e0;
  }
  .Product-table td{
    text-align: center;
    width: 50px;
    height: 30px;
    line-height: 30px;
    font-size: 12px;
    font-weight: bold;

  }

  .Product-table tr:nth-of-type(1){
    background: darkcyan;
    color: white;
  }
.Product-table tr:nth-of-type(1) td{
  font-size: 18px;
}
</style>



