<template>
  <el-container>
    <el-col :span="4">
     <div class="leftNav">
       <ul>
         <li v-for='item in navList'><router-link :to="{path:item.path}" tag="a">{{item.name}}<span>&gt</span></router-link></li>
       </ul>
       <div class="imgIcon">
         <img :src="production" alt=""/>
       </div>
     </div>
    </el-col>
    <el-col :span="20">
      <keep-alive>
        <router-view></router-view>
      </keep-alive>
    </el-col>
  </el-container>
</template>

<script type="text/ecmascript-6">
  mode:"history"
  import BroadList from '@/components/common/Broadcast'
    export default{
      data(){
    return{
      invtime: 2000,
      navList:[
        {name:'Kindle',path:"detail01"},
        {name:' ',path:"detail02"},
        {name:'',path:"detail03"},
        {name:'',path:"detail04"},
        {name:' ',path:"detail01"},
        {name:'',path:"detail02"},
        {name:'',path:"detail03"},
        {name:'',path:"detail04"},
        {name:'',path:"detail01"},
        {name:'',path:"detail02"}
      ],
      imgMap:{
        '/details/detail01':require('../../assets/images/imgIcon/01.jpg'),
        '/details/detail02':require('../../assets/images/imgIcon/02.jpg'),
        '/details/detail03':require('../../assets/images/imgIcon/03.jpg'),
        '/details/detail04':require('../../assets/images/imgIcon/04.jpg')
      }
    }
  },
  components:{

  },
  computed:{
    production(){
//      console.log(this.$route.path)
      return this.imgMap[this.$route.path]
    }
  },
  methods:{

  }
    }
</script>


<style>
.leftNav{
  width: 180px;
  padding: 15px;
  box-sizing: border-box;
  border: 1px solid #bbbbbb;
}
.leftNav li{
  margin-bottom: 12px;
  text-align: left;
}
.leftNav span{
  float: right;
}
  .leftNav a{
    width: 100%;
    display: block;
    font-size: 13px;
    color: black;
  }
  .leftNav a:hover,.active{
    font-size: 14px;
    font-weight: bold;
  }
  .imgIcon img{
    width: 148px;
    height: 96px;
  }
</style>



